.Date {
    font-size: 1.3rem;
    font-style: italic;
    animation-delay: .25s;
}

.Children {
    animation-delay: .5s;
}

.Children p {
    text-align: justify;
}

.Animation {
    animation-name: SlideIn;
    animation-duration: .5s;
    animation-timing-function: cubic-bezier(.78,.24,.43,.92);
    animation-fill-mode: forwards;
    animation-iteration-count: 1;

    transform: translateX(-12.8rem);
    opacity: 0;
}

@keyframes SlideIn {
    0% {
        transform: translateX(-12.8rem);
        opacity: 0;
    }
    80% {
        transform: translateX(1.2rem);
        opacity: 1;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}