.Introduction {
    text-align: center;
}

.ProfilePhoto {
    max-width: 25.6rem;
    border-radius: 100%;
    filter: drop-shadow(0 1.2rem 1.2rem rgba(0, 0, 0, 0.2));
    animation: SlideIn .5s ease-out forwards;
}

.Introduction > h1 {
    animation: SlideIn .25s ease-out forwards;
    animation-delay: .5s;
    transform: translateY(6.4rem);
    opacity: 0;
}

.Introduction > h2 {
    animation: SlideIn .25s ease-out forwards;
    animation-delay: .25s;
    transform: translateY(6.4rem);
    opacity: 0;
}

.Content p {
    text-align: justify;
}

@keyframes SlideIn {
    0% {
        transform: translateY(6.4rem);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}