.Nav {
    box-sizing: border-box;
    width: 100vw;
    padding: 1.2rem 2.4rem;
    background-color: #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Nav .Right a, .Nav .Left i {
    font-size: 2.4rem;
    margin-left: 1.2rem;
    text-decoration: none;
}

.Nav .Left {
    display: flex;
    align-items: center;
}

.Nav .Left > * {
    margin-right: 1.2rem;
}

@media screen and (max-width: 768px) {
    .Nav {
        flex-direction: column;
    }
}