.Expandable {
    max-height: 25.6rem;
    overflow: hidden;
    transition: all 1.5s ease-in-out;
    position: relative;
}

.Expandable::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    background: rgb(0,0,0);
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(255,255,255,1) 100%);
    height: 12.8rem;
    width: 100%;
    z-index: 1;
}

.Expanded {
    max-height: 500vh;
}

.Expanded::after {
    display: none;
}

.Expand {
    z-index: 10;
    color: #000;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    font-size: 2.4rem;

    animation: Bounce 1s ease-in-out;
    animation-iteration-count: infinite;
    color: #2F3640;
}

@keyframes Bounce {
    0% {
        transform: translateY(-1rem);
    }
    50% {
        transform: translateY(-0.5rem);
    }
    100% {
        transform: translateY(-1rem);
    }
}